<template>
  <div class="carosel--wrapper">
    <div
      v-if="nameContries != 'latinAmerica'"
      class="carosel--items"
      :style="caroselSlide"
    >
      <!-- v-if="caroselIndex === 0" -->

      <div
        :class="{
          'country--name-st': caroselIndex !== 0 && countryStyle <= 6,
          'country--name': true,
          'country--name-stb': caroselIndex !== 0 && countryStyle > 6,
        }"
      >
        {{ Coutries[0].name }}
        <!-- {{ countryStyle }} -->
      </div>
      <div
        :class="item.description ? 'carosel--item__descr' : 'carosel--item'"
        v-for="item in Coutries"
        :key="item.id"
      >
        <img v-if="item.img ? item.img : null" :src="item.img" alt="Cuba-img" />
        <div
          v-if="item.description ? item.description : null"
          class="descrtiption"
        >
          <span>{{ item.description }}</span>
        </div>
      </div>
      <div class="button--wrapper">
        <button class="arrow-left" @click="onClickPrew"></button>
        <button class="arrow-rigth" @click="onClickNext"></button>
      </div>
    </div>
    <div v-if="nameContries == 'latinAmerica'" class="carosel--items">
      <!-- v-if="caroselIndex === 0" -->
      <div
        class="carosel--item carosel--item_ab"
        v-for="item in Coutries"
        :key="item.id"
      >
        <img v-if="item.img ? item.img : null" :src="item.img" alt="Cuba-img" />
        <ul class="countries--wrapper">
          <h2 class="country--title">{{ Coutries[0].name }}</h2>
          <li class="country">{{ item.Honduras }}</li>
          <li class="country">{{ item.Columbia }}</li>
          <li class="country">{{ item.Panama }}</li>
          <li class="country">{{ item.Brazil }}</li>
          <li class="country">{{ item.Peru }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    Coutries: {
      type: Array,
      require: true,
    },
  },
  data() {
    return {
      caroselIndex: 0,
      caroselSlide: 0,
    };
  },
  methods: {
    onClickNext() {
      console.log(this.Coutries);
      this.caroselIndex >= this.Coutries.length - 1
        ? (this.caroselIndex = 0)
        : this.caroselIndex++;
      this.caroselSlide = `margin-left:-${100 * this.caroselIndex}%`;
    },
    onClickPrew() {
      this.caroselIndex === 0 ? (this.caroselIndex = 0) : this.caroselIndex--;
      this.caroselSlide = `margin-left:-${100 * this.caroselIndex}%`;
    },
  },

  computed: {
    nameContries() {
      return this.Coutries.map((item) => item.nameId);
    },
    countryStyle() {
      return this.Coutries[0].name.length;
    },
  },
};
</script>

<style>
.carosel--wrapper {
  position: relative;
  padding-top: 20px;
  width: 300px;
  height: 200px;
  overflow: hidden;
}
.carosel--items {
  display: flex;
  height: 100%;
  transition: all 0.5s;
}

.button--wrapper {
  display: flex;
  position: absolute;
  bottom: 19px;
  right: 19px;
}
.arrow-left {
  cursor: pointer;
  background: url("@/assets/icons/arrowLeft.png");
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  border: none;
  outline: none;
  margin-right: 15px;
  transition: 0.3s all;
}
.arrow-rigth {
  cursor: pointer;
  background: url("@/assets/icons/arrowRigth.png");
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  border: none;
  outline: none;
  transition: 0.3s all;
}
.arrow-rigth:hover,
.arrow-left:hover {
  transform: scale(1.1);
  transition: 0.3s all;
}
.country--name {
  letter-spacing: 3px;
  font-size: 36px;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: all 0.5s;
}
.country--name-st {
  letter-spacing: 3px;
  font-size: 36px;
  color: #fff;
  position: absolute;
  top: 80%;
  left: 24%;
  transform: translateX(-50%) translateY(-50%);
  transition: all 0.5s;
}
.country--name-stb {
  letter-spacing: 3px;
  font-size: 29px;
  color: #fff;
  position: absolute;
  top: 83%;
  left: 35%;
  transform: translateX(-50%) translateY(-50%);
  transition: all 0.5s;
}
.country--title {
  font-size: 36px;
  color: #fff;
}
.carosel--item__descr {
  position: relative;
}
.descrtiption {
  padding: 17px;
  position: absolute;
  color: #939495;
  top: 10px;
  left: 0;
  right: 0;
}
.carosel--item_ab {
  position: relative;
}
.countries--wrapper {
  position: absolute;
  top: 90px;
  left: 27px;
  list-style: none;
}
.country {
  padding: 20px 0px 0px 10px;
  font-size: 20px;
  color: #fff;
}
</style>


