<template>
  <div class="navigate">
    <ul class="nav--items" :style="navSlideStyle">
      <li
        :class="{ 'nav--item': true, 'nav--item_active': btn.active }"
        v-for="(btn, id) in navBtns"
        :key="id"
        @click="onActive(id)"
      >
        {{ btn.name }}
      </li>
    </ul>
    <div v-show="widthScreen <= 576" class="nav--arrow-wrapper">
      <button class="nav-arrow_left" @click="slideLeft">&larr;</button>
      <button class="nav--arrow_rigth" @click="slideRigth">&rarr;</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AppNavigate",
  data() {
    return {
      widthScreen: window.innerWidth,
      navSlideStyle: null,
      navBtns: [
        { name: "Все Страны", active: true },
        { name: "Культура" },
        { name: "Экстрим" },
        { name: "Атмосфера" },
        { name: "Люди" },
      ],
    };
  },
  methods: {
    size() {
      this.widthScreen = window.innerWidth;
    },
    slideLeft() {
      this.navSlideStyle = `margin-left:0`;
    },
    slideRigth() {
      this.navSlideStyle = `margin-left:-70%`;
    },
    onActive(id) {
      this.navBtns = this.navBtns.map((item, i) => {
        item.active = false;
        if (i == id) item.active = true;
        return item;
      });
    },
  },
  created() {
    window.addEventListener("resize", this.size);
  },
  destroyed() {
    window.addEventListener("resize", this.size);
  },
};
</script>

<style>
.nav--items {
  margin-top: 60px;
  display: flex;
  transition: 0.5s all;
  width: 400px;
}
.nav--item {
  z-index: 1000;
  padding-right: 24px;
  list-style: none;
  font-family: sans-serif;
  font-size: 12px;
  font-weight: bold;
  color: #939495;
}
.nav--item_active {
  color: #000;
  transition: 0.2s all;
}
.nav--item_active:after {
  content: "";
  margin-top: 4px;
  width: 100%;
  display: block;
  height: 2px;
  background: #000;
  transition: 0.2s all;
}
.nav--arrow-wrapper {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
  /* right: 1%; */
  left: 0%;
  transform: translateY(-30%) translateY(-50%);
  top: 5%;
}
.nav-arrow_left,
.nav--arrow_rigth {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  border: none;
  background: #697075;
  color: #ebf6ff;
  transition: 0.5s all;
  cursor: pointer;
}
.nav-arrow_left:hover,
.nav--arrow_rigth:hover {
  transform: scale(1.2);
  transition: 0.5s all;
}
</style>